.container{
    margin: 0 auto;
}
.nav{
    min-height: 100vh;
    min-width: 296px;
    position: fixed;
    border: 1px solid #000;
}
.www{
    margin-left: 296px;

}
.user-img{
    background-image: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week5/andychen.jpeg?raw=true);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-radius: 24px;
    height: 40px;
    width: 40px;
}
.nav-list{
   
    li{
        border: 1px solid red;
        // padding-left: 32px;
        &:hover::before{
            position: absolute;
            content: "";
            left: 0;
            width: 4px;
            height: 32px;
            border: 4px solid #198754;

        }
    }
    a{  
        position: relative;
        display: block;
        padding: 12px 32px;
    }
}